  margin: 0 var(--space-md) 0 0;
}

.GetCSSButton .icon {
  display: flex;
  margin-right: var(--space-xs);
}

.ResetButton {
  margin: 0 0 0 var(--space-md);
  padding: var(--space-xs) var(--space-sm);
}

.SettingsPaneFooter {
  align-items: center;
  background-color: var(--light-grey);
  display: flex;
  height: 4rem;
  justify-content: space-between;
}

.SettingsPane {
  background-color: var(--off-white);
  min-height: calc(100vh - 4rem);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  width: var(--side-pane-width);
}

.Main {
  display: flex;
  flex-direction: row;
  height: calc(100% - 2.074rem * 1.2 * 1.5);
  width: 100%;
}

.UndoButton {
  background-color: transparent;
  font-size: var(--font-xs);
  height: 36px;
  min-width: 64px;
  padding: 0;
  color: var(--orange);
  text-transform: uppercase;
}

.NotificationSnackbar {
  align-items: center;
  background-color: var(--black);
  border-radius: var(--border-radius-md);
  color: var(--white);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-height: 4.25rem;
  min-height: 3rem;
  padding: var(--space-sm) var(--space-xs) var(--space-sm) var(--space-md);
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  width: 21.5rem;
}

.NotificationSnackbar:not(:last-child) {
  margin-bottom: var(--space-xs);
}

.NotificationSnackbar .notification {
  align-items: center;
  display: flex;
  font-size: var(--font-xs);
  margin: 0 var(--space-xs) 0 0;
}

.NotificationSnackbar .controls {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.NotificationSnackbar .close-icon {
  color: var(--white);
  -webkit-transition: -webkit-transform 0.1s;
  transition: -webkit-transform 0.1s;
  transition: transform 0.1s;
  transition: transform 0.1s, -webkit-transform 0.1s;
}

.NotificationSnackbar .close-icon:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.NotificationSnackbar.elevate {
  -webkit-animation: elevate 0.4s forwards;
          animation: elevate 0.4s forwards;
}

.NotificationSnackbar.drop,
.NotificationSnackbar.undo {
  -webkit-animation: fadeOut 0.2s forwards;
          animation: fadeOut 0.2s forwards;
}

.Notifications {
  bottom: 1em;
  left: calc(100vw / 2 - 21.5em / 2);
  position: absolute;
  z-index: 999;
}

.App {
  -webkit-animation: fadeIn 0.2s;
          animation: fadeIn 0.2s;
  height: 100vh;
  position: relative;
  width: 100vw;
}

:root {
  --base: 16px;

  /* Typography */
  --scale-ratio: 1.2;

  --base-font-size: var(--base);

  --font-xs: calc(var(--base-font-size) / var(--scale-ratio));
  --font-sm: var(--base-font-size);
  --font-md: calc(var(--font-sm) * var(--scale-ratio));
  --font-lg: calc(var(--font-md) * var(--scale-ratio));
  --font-xl: calc(var(--font-lg) * var(--scale-ratio));
  --font-xxl: calc(var(--font-xl) * var(--scale-ratio));

  /* Spacing */
  /* 4px */
  --space-xxs: calc(0.25 * var(--base));
  /* 8px */
  --space-xs: calc(0.5 * var(--base));
  /* 12px */
  --space-sm: calc(0.75 * var(--base));
  /* 20px */
  --space-md: calc(1.25 * var(--base));
  /* 32px */
  --space-lg: calc(2 * var(--base));
  /* 52px */
  --space-xl: calc(3.25 * var(--base));
  /* 84px */
  --space-xxl: calc(5.25 * var(--base));

  /* Colors */
  --black: #212934;
  --off-black: #5f6b7a;
  --dark-grey: #5f6b7a;
  --grey: #b8c4ce;
  --light-grey: #cfd6de;
  --off-white: #e1e7ec;
  --almost-white: #f1f3f5;
  --white: #ffffff;

  --blue: #20639b;
  --green: #259d58;
  --orange: #caa53d;
  --red: #f01e4a;

  --error-background: #fffcf4;
  --error-border: #f4ca64;
  --error-text: #8d6f22;

  /* Box-shadow */
  --active-input-shadow: 0 0 3px 0 var(--blue);
  --inactive-input-shadow: 0 0 0 1px var(--grey);
  --hovered-button-shadow: 0 2px 4px 0 var(--dark-grey);
  --popup-shadow: 0 2px 6px 0 var(--light-grey);

  /* Border-radius */
  --border-radius-sm: 2px;
  --border-radius-md: 4px;
  --border-radius-lg: 6px;
  --border-radius-max: 50%;

  /* Miscellaneous */
  --blur-text: 0 0 20px var(--off-black);
  --side-pane-width: 22rem;
}

/* Animaitons */
@-webkit-keyframes elevate {
  from {
    -webkit-transform: translateY(10rem);
            transform: translateY(10rem);
  }
}
@keyframes elevate {
  from {
    -webkit-transform: translateY(10rem);
            transform: translateY(10rem);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeOut {
  to {
    opacity: 0;
  }
}

@keyframes fadeOut {
  to {
    opacity: 0;
  }
}

@-webkit-keyframes growY {
  from {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }

  to {
    -webkit-transform: scaleY(100%);
            transform: scaleY(100%)
  }
}

@keyframes growY {
  from {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }

  to {
    -webkit-transform: scaleY(100%);
            transform: scaleY(100%)
  }
}

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }

  50% {
    -webkit-transform: scale(1.4, 1.4);
            transform: scale(1.4, 1.4);
  }

  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
}

@keyframes spinner {
  0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }

  50% {
    -webkit-transform: scale(1.4, 1.4);
            transform: scale(1.4, 1.4);
  }

  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
}

* {
  box-sizing: border-box;
}

:root {
  font-size: var(--base);
}

body {
  background-color: var(--white);
  color: var(--black);
  font-family: "Quicksand", sans-serif;
  font-weight: 400;
  height: 100%;
  line-height: 1.5;
  overflow: hidden;
  width: 100%;
}

p {
  margin-bottom: 1.25em;
}

h1,
h2,
h3,
h4 {
  font-weight: 400;
  line-height: 1.2;
  margin: 2.75rem 0 1rem;
}

h1 {
  margin-top: 0;
  font-size: var(--font-xxl);
}

h2 {
  font-size: var(--font-xl);
}

h3 {
  font-size: var(--font-lg);
}

h4 {
  font-size: var(--font-md);
}

small,
.font-small {
  font-size: var(--font-xs);
}

input[type="number"],
input[type="text"],
select {
  background-color: var(--white);
  border: none;
  border-radius: var(--border-radius-sm);
  box-shadow: var(--inactive-input-shadow);
  height: 2rem;
  padding: 0.25rem 0.5rem;
  -webkit-transition: box-shadow 0.1s;
  transition: box-shadow 0.1s;
}

input[type="checkbox"] {
  opacity: 0;
  width: 0;
  -moz-appearance: none;
  -webkit-appearance: none;
}

input[type="number"]:focus,
input[type="text"]:focus,
select:focus {
  box-shadow: var(--active-input-shadow);
  outline: none;
  -webkit-transition: box-shadow 0.1s;
  transition: box-shadow 0.1s;
}

button:disabled,
input:disabled,
select:disabled {
  color: var(--light-grey);
  cursor: default;
}

button {
  align-items: center;
  border: none;
  cursor: pointer;
  display: flex;
  font-weight: 700;
  justify-content: center;
}

button:active,
button:focus {
  outline: none;
}

/* Buttons */
.btn-blue-rect {
  background-color: var(--blue);
  border-radius: var(--border-radius-md);
  box-shadow: none;
  color: var(--white);
  flex: 0 0 auto;
  height: 2.5rem;
  padding: 0 var(--space-sm);
  -webkit-transition: box-shadow 0.1s;
  transition: box-shadow 0.1s;
}

.btn-blue-rect:focus,
.btn-blue-rect:hover {
  box-shadow: var(--hovered-button-shadow);
  -webkit-transition: box-shadow 0.1s;
  transition: box-shadow 0.1s;
}

.btn-blue-rect:disabled {
  background-color: var(--grey);
  box-shadow: none;
  color: var(--off-white);
}

.btn-no-background {
  background: none;
  color: var(--black);
  -webkit-transition: color 0.1s;
  transition: color 0.1s;
}

.btn-no-background:hover {
  color: var(--red);
  -webkit-transition: color 0.1s;
  transition: color 0.1s;
}

.btn-no-background:disabled {
  color: var(--dark-grey);
}

.btn-round-big {
  border-radius: var(--border-radius-max);
  height: 2.5rem;
  padding: 0;
  -webkit-transition: background-color 0.1s, color 0.1s;
  transition: background-color 0.1s, color 0.1s;
  width: 2.5rem;
}

.btn-round-big:focus,
.btn-round-big:hover {
  background-color: var(--blue);
  color: var(--white);
  -webkit-transition: background-color 0.1s, color 0.1s;
  transition: background-color 0.1s, color 0.1s;
}

.btn-round-small {
  background-color: transparent;
  border-radius: var(--border-radius-max);
  height: 1.5rem;
  padding: 0;
  width: 1.5rem;
}



